.terminal {
    background-color: #000;
    color: #fff;
    font-family: monospace;
    padding: 20px;
    border-radius: 8px;
    width: 50%;
    height: 600px;
    margin: 0 auto;
    overflow: hidden;
    white-space: nowrap;
    font-size: 18px;
  }
  
  .terminal-text {
    animation: terminal-typing 4s linear forwards, typing 2s steps(40, end), blink-caret 0.5s step-end infinite;
    overflow: hidden;
    border-right: 1px solid white;
    white-space: nowrap;
  }
  

  .terminal-text-container {
    height: 600px;
    overflow-y: auto;
  }

  .terminal-text-container::-webkit-scrollbar {
    width: 0;
    background: transparent;
  }
  
  .terminal-text-container {
    scrollbar-width: none;
  }

  .terminal-text a {
    color: #0074d9;
    text-decoration: none;
  }
  
  @keyframes typing {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }
  
  @keyframes blink-caret {
    from, to {
      border-color: transparent;
    }
    50% {
      border-color: white;
    }
  }
  @keyframes terminal-typing {
    0% {
      visibility: hidden;
    }
    100% {
      visibility: visible;
    }
  }